import React, { Component } from 'react';

class Info extends Component {
    render() {
        return (
            <p>
                「寝落ちゃっと」は、おやすみなさいから始まる、寝落ち専用チャットアプリです。
                先におやすみなさいをお互いに行ってから、決まった時間だけ喋って、寝落ちしましょう。
            </p>
        );
    }
}

export default Info;



